import CookieStore from './CookieStore'

/**
 * å¾®ä¿¡ Cookie ä»£ç
 * @param  {Object} wx      å¾®ä¿¡ API å¯¹è±¡
 * @param  {Object} request å¾®ä¿¡è¯·æ±å½æ°
 */
const cookieStore = (function (wx, request) {
  // åå»º cookieStore å®ä¾
  const cookieStore = new CookieStore()

  /**
   * å®ä¹è¯·æ±ä»£çå½æ°
   * @param  {Object} options è¯·æ±åæ°
   */
  function requestProxy (options) {
    // æ¯å¦å¯ç¨ cookieï¼é»è®¤ trueï¼
    options.cookie = options.cookie === undefined || !!options.cookie
    options.dataType = options.dataType || 'json'
    if (options.cookie) {
      // åå
      let domain = (options.url || '').split('/')[2]

      // è·åè¯·æ± cookies
      let requestCookies = cookieStore.getRequestCookies(domain)

      // è¯·æ±æ¶å¸¦ä¸è®¾ç½®ç cookies
      options.header = options.header || {}
      options.header['Cookie'] = requestCookies
      options.header['X-Requested-With'] = 'XMLHttpRequest'
      if (options.dataType === 'json') {
        options.header['Accept'] = 'application/json, text/plain, */*'
      }

      // è¯·æ±æååè°
      let successCallback = options.success
      options.success = function (response) {
        // è·åååº cookies
        let responseCookies = response.header['Set-Cookie'] || response.header['set-cookie'] || ''
        // è®¾ç½® cookiesï¼ä»¥ä¾¿ä¸æ¬¡è¯·æ±å¸¦ä¸
        cookieStore.setResponseCookies(domain, responseCookies)
        successCallback && successCallback(response)
      }
    }

    // åéç½ç»è¯·æ±
    request(options)
  }

  // ä½¿ç¨ requestProxy è¦çå¾®ä¿¡åç request
  Object.defineProperties(wx, {
    request: {
      value: requestProxy
    }
  })

  // è¿å cookieStore
  return cookieStore
})(wx, wx.request)

// å¯¼åº cookieStore å®ä¾
export default cookieStore
