import Cookie from './Cookie'
import cookieParser from 'set-cookie-parser'

/**
 * CookieStore ç±»
 */
class CookieStore {
  /**
   * æé å½æ°
   */
  constructor () {
    // storageKey
    this.__storageKey = '__cookie_store__'
    // cookies Mapç¼å­ï¼domain -> cookie äºçº§ç»æï¼
    this.__cookiesMap = this.__readFromStorage()
  }

  /**
   * æ¯å¦å­å¨æä¸ª cookie
   * @param  {String}  name       cookie åç§°
   * @param  {String}  [domain]   æå®ååï¼å¯éï¼
   * @return {Boolean}            æ¯å¦å­å¨
   */
  has (name, domain) {
    // è¿åæ¯å¦å­å¨ cookie å¼
    return this.get(name, domain) !== undefined
  }

  /**
   * è·ååå cookie
   * @param {String} name       cookie åç§°
   * @param {String} [domain]   æå®ååï¼å¯éï¼
   * @return {String}           cookie å¼
   */
  get (name = '', domain) {
    let cookie = null

    // è·å cookie scope ååæ°ç»
    let scopeDomains = this.__getCookieScopeDomain(domain)

    // è·åä»»æååç cookie
    for (let [key, cookies] of this.__cookiesMap.entries()) {
      // å¦ææååï¼åæ ¹æ®ååè¿æ»¤
      if (domain && scopeDomains.indexOf(key) < 0) continue
      // è·å cookie
      cookie = cookies.get(name)
      if (cookie) break
    }

    // è¿å cookie å¼
    return cookie ? cookie.value : undefined
  }

  /**
   * è®¾ç½®åå cookie
   * @param {String} name       cookie åç§°
   * @param {String} value      cookie å¼
   * @param {Object} [options]  cookie éé¡¹
   * @return {Cookie}           cookie å¯¹è±¡
   */
  set (name, value, options) {
    // æå»º Cookie å®ä¾
    let domain = options.domain
    if (!domain || !name) throw new Error('name å options.domain å¼ä¸æ­£ç¡®ï¼')

    let cookie = new Cookie(Object.assign(options, {
      name: name,
      value: value
    }))

    // è®¾ç½®å°æå®åå
    let cookies = this.__cookiesMap.get(domain) || new Map()
    cookies.set(name, cookie)
    this.__cookiesMap.set(domain, cookies)

    // ä¿å­å° Storage
    this.__saveToStorage()

    return cookie
  }

  /**
   * æå°ææ cookies ç»æ
   * @return {Object}   dirObj
   */
  dir () {
    let dirObj = { }

    for (let domain of this.__cookiesMap.keys()) {
      dirObj[domain] = this.getCookies(domain)
    }

    return dirObj
  }

  /**
   * å é¤ cookie
   * @param  {Array}  name      cookie é®
   * @param  {String} [domain]  æå®ååï¼å¯éï¼
   * @return {Boolean}           å é¤æå
   */
  remove (name, domain) {
    if (domain) {
      // å é¤æå®ååç cookie
      let cookies = this.__cookiesMap.get(domain)
      cookies && cookies.delete(name)
    } else {
      // å é¤ææååç cookie
      for (let cookies of this.__cookiesMap.values()) {
        cookies.delete(name)
      }
    }

    // ä¿å­å° Storage
    this.__saveToStorage()

    return true
  }

  /**
   * è·å cookies key/value å¯¹è±¡
   * @param  {String} [domain]  æå®ååï¼å¯éï¼
   * @return {Object}           cookies
   */
  getCookies (domain) {
    let cookieValues = { }

    // å° cookie å¼æ·»å å°å¯¹è±¡
    this.getCookiesArray(domain).forEach((cookie) => {
      cookieValues[cookie.name] = cookie.value
    })

    // è¿åè·åç cookie å¼å¯¹è±¡
    return cookieValues
  }

  /**
   * è·å cookies å¯¹è±¡æ°ç»
   * @param  {String} [domain]  æå®ååï¼å¯éï¼
   * @return {Object}           cookies
   */
  getCookiesArray (domain) {
    let cookiesArr = []

    if (domain) {
      // è·å cookie scope ååæ°ç»
      let scopeDomains = this.__getCookieScopeDomain(domain)

      // è·åæå®ååèå´ç cookies
      scopeDomains.forEach((domain) => {
        let cookies = this.__cookiesMap.get(domain)
        if (!cookies) return
        for (let cookie of cookies.values()) {
          if (cookie.validate()) cookiesArr.push(cookie)
        }
      })
    } else {
      // è·åææååç cookie å¼
      for (let cookies of this.__cookiesMap.values()) {
        for (let cookie of cookies.values()) {
          if (cookie.validate()) cookiesArr.push(cookie)
        }
      }
    }

    // è¿åè·åç cookie å¼å¯¹è±¡
    return cookiesArr
  }

  /**
   * è®¾ç½® cookies å¯¹è±¡æ°ç»å° store
   * @param  {Array} cookies  Cookie å¯¹è±¡æ°ç»
   * @return {Map}            cookies Map å¯¹è±¡
   */
  setCookieArray (cookies = []) {
    this.__cookiesMap = this.__cookiesMap || new Map()

    // Cookie æ°ç»è½¬æ¢ Map å¯¹è±¡
    cookies.forEach((cookie) => {
      let cookieMap = this.__cookiesMap.get(cookie.domain)
      if (!cookieMap) {
        cookieMap = new Map()
        this.__cookiesMap.set(cookie.domain, cookieMap)
      }
      cookieMap.set(cookie.name, cookie)
    })

    // ä¿å­å° Storage
    this.__saveToStorage()

    return this.__cookiesMap
  }

  /**
   * æ¸é¤ cookies
   * @param  {String} [domain]  æå®ååï¼å¯éï¼
   * @return {Boolean}          æ¸é¤æå
   */
  clearCookies (domain) {
    if (domain) {
      let cookies = this.__cookiesMap.get(domain)
      cookies && cookies.clear()
    } else {
      this.__cookiesMap.clear()
    }

    // ä¿å­å° Storage
    this.__saveToStorage()

    return true
  }

  /**
   * è·å request cookies
   * @param  {String} domain æå®åå
   * @return {String}        request cookies å­ç¬¦ä¸²
   */
  getRequestCookies (domain) {
    // cookies æ°ç»
    let cookiesArr = this.getCookiesArray(domain)

    // è½¬åä¸º request cookies å­ç¬¦ä¸²
    return this.stringify(cookiesArr)
  }

  /**
   * è®¾ç½® response cookies
   * @param {String} setCookieStr response set-cookie å­ç¬¦ä¸²
   * @param {String} domain       é»è®¤ååï¼å¦æ set-cookie ä¸­æ²¡æè®¾ç½® domain åä½¿ç¨è¯¥ååï¼
   */
  setResponseCookies (setCookieStr, domain) {
    // è½¬æ¢ä¸º cookie å¯¹è±¡æ°ç»
    let parsedCookies = this.parse(setCookieStr, domain)

    // è®¾ç½® cookies
    return this.setCookieArray(parsedCookies)
  }

  /**
   * è§£æ response set-cookie å­æ®µ
   * @param  {String} setCookieStr response set-cookie å­ç¬¦ä¸²
   * @param  {String} domain       é»è®¤ååï¼å¦æ set-cookie ä¸­æ²¡æè®¾ç½® domain åä½¿ç¨è¯¥ååï¼
   * @return {Array}               Cookie å¯¹è±¡æ°ç»
   */
  parse (setCookieStr = '', domain) {
    // parse
    var cookies = cookieParser.parse(cookieParser.splitCookiesString(setCookieStr))

    // è½¬æ¢ä¸º Cookie å¯¹è±¡
    return cookies.map((item) => {
      if (!item.domain) item.domain = domain
      return new Cookie(item)
    })
  }

  /**
   * å° cookies å­ç¬¦ä¸²åï¼è½¬åä¸º request cookies å­ç¬¦ä¸²
   * @param  {Array} cookies Cookie å¯¹è±¡æ°ç»
   * @return {String}        cookie å­ç¬¦ä¸²
   */
  stringify (cookies) {
    return cookies.map((item) => item.toString()).join('; ')
  }

  /**
   * å° cookies ä¿å­å° Storage
   */
  __saveToStorage () {
    let saveCookies = []

    // è·åéè¦æä¹åç cookie
    for (let cookies of this.__cookiesMap.values()) {
      for (let cookie of cookies.values()) {
        if (cookie.validate()) {
          // åªå­å¨å¯æä¹å cookie
          if (cookie.isPersistence()) saveCookies.push(cookie)
        } else {
          // æ¸é¤æ æ cookie
          cookies.delete(key)
        }
      }
    }

    // ä¿å­å°æ¬å°å­å¨
    wx.setStorageSync(this.__storageKey, saveCookies)
  }

  /**
   * ä» Storage è¯»å cookies
   */
  __readFromStorage () {
    // ä»æ¬å°å­å¨è¯»å cookie æ°æ®æ°ç»
    let cookies = wx.getStorageSync(this.__storageKey) || []

    // è½¬åä¸º Cookie å¯¹è±¡æ°ç»
    cookies = cookies.map((item) => new Cookie(item))

    // è½¬åä¸º cookie map å¯¹è±¡
    return this.setCookieArray(cookies)
  }

  /**
   * æ ¹æ®ååè·åè¯¥ååç cookie ä½ç¨åèå´åè¡¨
   * @param  {String} domain æå®åå
   * @return {String}        cookie ä½ç¨åèå´åè¡¨
   */
  __getCookieScopeDomain (domain = '') {
    if (!domain) return []

    // è·å cookie ä½ç¨åèå´åè¡¨
    domain = domain.replace(/^\.+/ig, '')
    let scopes = domain.split('.').map(k => ['.', domain.slice(domain.indexOf(k))].join(''))

    return [domain].concat(scopes)
  }
}

export default CookieStore
